.toDoBox{
    width:100%;
    height: 100px;
    border-top: 1.5px solid black;
    padding: 10px 0px;
    display: flex;
    flex-flow:wrap;
    justify-content: center;
    align-items: flex-start;
    background: inherit;
}
.toDoBox:first-child{
    margin-top:30px;
}

.toDo{
    width:90%;
    margin: 0px 10px;
    list-style: none;
    background: inherit;
    font-size: 26px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
}

.btnBox{
    width: 100px;
    height: 40px;
    background: none;
    border-radius: 20px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.toDoList{
    width: 100%;
    height: 60%;
    background: rgba(0,0,0,0.4);
    display: flex;
    flex-flow: column;
    justify-content: flex-start;
    align-items: center;
}

.toDoBoxContainer{
    height: 100%;
    width: 60%;
    background: none;
    padding: 0px 10px;
    overflow : scroll;
    overflow-x : hidden;
}

@media (max-width: 1000px) {
    .toDoBoxContainer{
        width: 80%;
    }
}
html, body{
    width:100vw;
    height: 100vh;
    box-sizing: border-box;
    background: none;
    
}

* {
    font-family: 'Rubik', sans-serif;
}

body{
    min-height: 100%;
    
}


#root{
    width: 100vw;
    height: 100vh;
    background-image: url(/react-todo/static/media/sheep1600.4fcea8aa.jpg);
    background-repeat: no-repeat;
    background-size: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

@media (max-width: 1000px) {
    #root{
        background-image: url(/react-todo/static/media/sheepphone.a701a2f7.jpg);
        background-repeat: no-repeat;
        background-size: 100%;
        overflow: hidden;
    }
}

* *::before *::after{
    box-sizing: inherit;
}

::-webkit-scrollbar-button{
    background: none;
}

::-webkit-scrollbar-thumb{
    background: rgba(0, 0, 0, 0.3);
    border: none;
    border-radius: 10px;
}

::-webkit-scrollbar-track{
    background: none;
}

::-webkit-scrollbar{
    width: 8px;
}
